<template>
	<div class="panel">
		<h2>畅销商家分布</h2>
		<div ref="pieChart2" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'PiePanel2',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.pieChart2);
				if(!chart){
					chart = this.$echarts.init(this.$refs.pieChart2);
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					color: ['#006cff', '#60cda0', '#ed8884', '#ff9f7f', '#0096ff', '#9fe6b8', '#32c5e9', '#1d9dff'],
					tooltip: {
						trigger: 'item',
						formatter: "<b>{a}</b> <br/> {b} : {c} <i>({d}%)</i>",
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					series: [{
						name: '商家',
						type: 'pie',
						radius: ["15%", "85%"],
						center: ['50%', '50%'],
						roseType: 'radius',
						itemStyle: {
							borderRadius: 5
						},
						label: {
							color: 'white',
							fontSize: 10
						},
						labelLine: {
							length: 6,
							length2: 8
						},
						data: this.list
					}]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/seller.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list.push({
						name: resp.data[i][0],
						value: resp.data[i][1]
					});
				}
				this.drawChart();
			})
		}
	}
</script>

